import { useTagged } from 'react-tagged-state';
import boardState from '../states/boardState';
import gameOverState from '../states/gameOverState';
import Cell from './Cell';
import StyledBoard from './StyledBoard';
import EmptyBoard from './EmptyBoard';

const Board = () => {
    const board = useTagged(boardState);
    const gameOver = useTagged(gameOverState);

    if (!board) {
        return <EmptyBoard />;
    }

    return (
        <StyledBoard
            style={{ '--rows': board.board.length, '--columns': board.board[0].length }}
            data-is-over={!!gameOver}
        >
            {/* eslint-disable-next-line react/jsx-key */}
            {board.board.map((row) => row.map((cell) => <Cell key={cell.id} cell={cell} />))}
        </StyledBoard>
    );
};

export default Board;
